:root {
  --color-light: #eee;
  --color-dark: #1a1a1a;
  --color-accent: tomato;
}

body {
  max-height: 100vh;
  background-color: #eee;
}

.hidden {
  display: none;
}

.cat-info {
  max-width: 300px;
  margin: 0 auto;
}

.img {
  box-shadow: 0 0 10px var(--color-accent);
  max-width: 50%;
  border-radius: 15px;
  margin: 0 auto;
  display: block;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #aaa;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: absolute;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 40px;
  height: 40px;
  border: 3px solid #0000;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.hidden {
  display: none;
}

.error {
  color: #fff;
  background: #ff5549;
}

